import cn from 'classnames';
function TrainingCard({
  data = [],
  checkAnswerHandler,
}) {
  return (
    <div
      className='border border-indigo-600 rounded w-full'
    >
      <div className='flex justify-center items-center h-60 text-8xl m-4 p-1'>
        { data.jp }
      </div>
      <div className='flex border-t border-indigo-600'>
        {
          data.options.map((option, idx) => (
            <div
              key={idx}
              className={cn(
                'w-full text-center cursor-pointer hover:bg-indigo-100',
                {
                  'border-l border-indigo-600': idx > 0
                }
              )}
              onClick={() => checkAnswerHandler(option.isCorrect)}
            >
              {
                (option.onyomi || option.kunyomi) &&
                  <div className='flex flex-col justify-center items-center h-40 border-b border-indigo-600'>
                    <div className='mb-4'>
                      Onyomi:
                      <br/>
                      <b>{ option.onyomi || <>&mdash;</> }</b>
                    </div>
                    <div>
                      Kunyomi:
                      <br/>
                      <b>{ option.kunyomi || <>&mdash;</> }</b>
                    </div>
                  </div>
              }
              <div className='flex justify-center items-center h-10'>
                <b>{ option.en }</b>
              </div>
            </div>    
          ))
        }
      </div>
    </div>
  )
}

export { TrainingCard }