import cn from 'classnames'
function LearningCard({
  data,
  blind,  
}) {
  return (
    <div className='group border border-indigo-600 rounded hover:bg-indigo-100 w-full p-1 cursor-pointer'>
      <div
        className={cn(
          {
            'opacity-0': blind
          },
          'group-hover:opacity-100 transition-all duration-200 flex justify-center text-8xl m-4'
        )}
        style={{willChange: 'opacity'}}
      >
        { data.jp }
      </div>
      {
        (data.onyomi || data.kunyomi) &&
          <div className='border-t border-b border-indigo-600'>
            <div>
              Onyomi: { data.onyomi }
            </div>
            <div>
              Kunyomi: { data.kunyomi }
            </div>
          </div>
      }
      <div className='flex justify-center'>
        { data.en }
      </div>
    </div>
  )
}

export { LearningCard }