import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useLocation
} from 'react-router-dom'
import cn from 'classnames'

import { CollectionsProvider } from './collections/CollectionsProvider';
import Home from './pages/index'
import Training from './pages/training'
import Learning from './pages/learning'

function Nav() {
  const location = useLocation()
  
  return (
    <header className='border-b shadow-lg'>
      <div className='container mx-auto flex px-4 py-5'>
        <div className='mr-12'>
          正解
        </div>
        <nav>
          <ul className='flex'>
            <li className='mr-6'>
              <Link
                to='/'
                className={cn(
                  'font-semibold',
                  {'text-red-500': location.pathname === '/' /* TODO: rework with useRouteMatch */}
                )}
              >Home</Link>
            </li>
            <li className='mr-6'>
              <Link
                to='/learning'
                className={cn(
                  'font-semibold',
                  {'text-red-500': location.pathname === '/learning'}
                )}
              >Learning</Link>
            </li>
            <li className='mr-6'>
              <Link
                to='/training'
                className={cn(
                  'font-semibold',
                  {'text-red-500': location.pathname === '/training'}
                )}
              >Training</Link>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  )
}

function App() {
  return (
    <CollectionsProvider>
      <Router>
        <Nav/>
        <div className='container mx-auto px-4'>
          <main className='py-16'>
            <Switch>
              <Route exact path='/'>
                <Home/>
              </Route>
              <Route exact path='/training'>
                <Training/>
              </Route>
              <Route exact path='/learning'>
                <Learning/>
              </Route>
            </Switch>
          </main>  
        </div>
      </Router>
    </CollectionsProvider>
  );
}

export default App