import { useEffect, useState } from 'react'
import Select from 'react-select'
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab'

import { useCollections } from '../collections/CollectionsProvider'
import { TrainingCard } from '../cards/TrainingCard'
// import ABC from '../json/abc.json'

// https://javascript.info/task/shuffle
function shuffle(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }

  return array
}

function pick(arr, exclude) {
  if (arr.length === 0) return null

  if (!Array.isArray(exclude)) {
    return arr[Math.floor(Math.random() * arr.length)]
  } else {
    const p = arr[Math.floor(Math.random() * arr.length)]

    if (exclude.indexOf(p) === -1) {
      return p
    } else {
      return pick(arr, exclude)
    }
  }
}

export default function Training() {
  const [stateTotalAnswers, setTotalAnswers] = useState(0)
  const [stateCorrectAnswers, setCorrectAnswers] = useState(0)
  const [stateDifficulty, setDifficulty] = useState(2)
  const [statePick, setPick] = useState({options: []})
  const [stateCollection, setCollection] = useState([])
  const collections = useCollections()
  
  useEffect(() => {
    if (stateCollection.length > 0) {
      const p = pick(stateCollection)
      let options = [p]
  
      for (let i = 0; i < stateDifficulty - 1; i++) {
        options.push(pick(stateCollection, options));
      }

      options = options.map((option, idx) => ({
        onyomi: option.onyomi, 
        kunyomi: option.kunyomi,
        en: option.en,
        isCorrect: !idx
      }))
      
      options = shuffle(options)

      setPick({
        jp: p.jp,
        options
      })
    }
  }, [stateTotalAnswers, stateCollection, stateDifficulty])

  console.log(statePick);

  function checkAnswerHandler(isCorrect) {
    setTotalAnswers(stateTotalAnswers + 1)
    if (isCorrect) {
      setCorrectAnswers(stateCorrectAnswers + 1)
    }
  }

  return (
    <>
      <div>Total answers count: <b>{ stateTotalAnswers }</b></div>
      <div className='mb-8 text-green-600'>Correct answers count: <b>{ stateCorrectAnswers }</b></div>
      <div className='flex justify-center mb-4'>
        <ToggleButtonGroup
          value={stateDifficulty}
          exclusive
          onChange={(event, value) => setDifficulty(value)}
          aria-label='difficulty'
          className='w-full md:w-1/2 lg:w-1/3'
        >
          <ToggleButton value={2} aria-label='easy' className='w-full'>
            <div className=''>
              Easy
            </div>
          </ToggleButton>
          <ToggleButton value={3} aria-label='medium' className='w-full'>
            <div className=''>
              Medium
            </div>
          </ToggleButton>
          <ToggleButton value={4} aria-label='hard' className='w-full'>
            <div className=''>
              Hard
            </div>
          </ToggleButton>
        </ToggleButtonGroup>
      </div>
      <div className='flex justify-center items-center mb-8'>
        <div className='w-full md:w-1/2 lg:w-1/3'>
          <Select
            placeholder='Select collection...'
            options={ collections.index }
            onChange={(value) => { setCollection(collections.data[value.value]) }}
          />
        </div>
      </div>
      {
        statePick.options.length === 0 &&
        <div
          className='text-3xl italic text-center text-gray-400'
        >
          Please, select collection to practice
        </div>
      }
      {
        statePick.options.length > 0 &&
          <div className='flex justify-center'>
            <div className='w-full md:w-1/2 lg:w-1/3'>
              <TrainingCard
                data={statePick}
                checkAnswerHandler={checkAnswerHandler}
              />
            </div>
          </div>
      }
    </>
  )
}